import { splitLocale } from './locale';

describe('locale', () => {
  it('parses a normal locale string', () => {
    expect(splitLocale('de-DE')).toEqual({ country: 'DE', language: 'de' });
  });

  it('converts a two letter region into the full string', () => {
    expect(splitLocale('en-WW')).toEqual({ country: 'international', language: 'en' });
  });

  it('formats a region string correctly', () => {
    expect(splitLocale('en-MIDDLE-east')).toEqual({ country: 'middle-east', language: 'en' });
  });

  it('throws if empty string is passed', () => {
    expect(() => splitLocale('')).toThrow();
  });

  it('throws if locale is invalid', () => {
    expect(() => splitLocale('en')).toThrow();
  });
});
